body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: #e6e8ed;
}

.App {
    background-color: #e6e8ed;
    padding-bottom: 50px;
}

.white-bg {
    background-color: white;
}
#header {
    height: 65px;
    width: 90vw;
    margin: auto;
    border-bottom: 1px solid beige;
    margin-bottom: 10px;
}

#header h1 {
    margin-left: 20px;
    margin-top: 0;
}
#loginForm {
    width: 500px;
    height: 120px;
}

.div-under-loginForm {
    display: grid;
    grid-template-columns: 30% 70%;
    grid-template-rows: 30px 30px 30px;
    grid-column-gap: 30px;
    grid-row-gap: 10px;
}

.label {
    text-align: right;
    font-weight: 600;
}

.submitButton {
    grid-column-start: 2;
    grid-column-end: 2;
    width: 50px;
}

#customerDetailsPage {
    margin-left: 25px;
}
#basicInfo {
    width: 600px;
    height: 320px;
    border: 1.3px solid #D0E8F5;
    border-radius: 2px;
    margin: 0 0 25px 50px;
}

.div-under-basicInfo {
    display: grid;
    grid-template-columns: 20% 70%;
    grid-template-rows: 30px 30px 30px 30px 30px 30px;
    grid-column-gap: 30px;
    grid-row-gap: 15px;
}

#basicInfo p {
    margin: 0 0 15px 0;
    background-color: #D0E8F5;
    color: #75A3BB;
    font-weight: 500;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-top: 10px;
}

.div-under-basicInfo label {
    justify-self: end;
    font-weight: 500;
}

.div-under-basicInfo input {
    border: 1.5px solid #CDD6D5;
    border-radius: 3px;
}

.saveButton {
    grid-area: 6 / 2 / 6 / 2;
    width: 60px;
}
#passengers {
    width: 1070px;
    border: 1.3px solid #D0E8F5;
    border-radius: 2px;
    display: flex;
    flex-flow: column;
    margin: 0 0 25px 50px;
}

#passengers p {
    background-color: #D0E8F5;
    color: #75A3BB;
    font-weight: 500;
    margin: 0 0 15px 0;
    padding-top: 5px;
    padding-bottom: 10px;
    padding-left: 10px;
}

table#table-passengers {
    border-collapse: collapse;
    width: 1050px;
    margin-left: 10px;
}

th {
    text-align: left;
}

.passengersTBody {
    align-content: center;
}

table tr:nth-child(odd) {
    background-color: #E0E0E0;
}

.addButton {
    height: 30px;
    width: 40px;
    align-self: flex-end;
    margin-right: 15px;
    margin-top: 25px;
    margin-bottom: 10px;
    background-color: #54A454;
    color: white;
    border: none;
    border-radius: 4px;
}

.deleteButton {
    width: 70px;
    height: 30px;
    background-color: #C1322E;
    color: white;
    border: none;
    border-radius: 4px;
    margin-top: 5px;
    margin-bottom: 5px;
}





#trips {
    width: 1070px;
    border: 1.3px solid #D0E8F5;
    border-radius: 2px;
    display: flex;
    flex-flow: column;
    margin: 0 0 25px 50px;
}

#trips p {
    background-color: #D0E8F5;
    color: #75A3BB;
    font-weight: 500;
    margin: 0 0 15px 0;
    padding-top: 5px;
    padding-bottom: 10px;
    padding-left: 10px;
}

table#table-trips {
    border-collapse: collapse;
    width: 1050px;
    margin-left: 10px;
}

th {
    text-align: left;
}

.tripsTBody {
    align-content: center;
}

table tr:first-child {
    background-color: #E0E0E0;
}
#addTrip {
    width: 800px;
    border: 1.3px solid black;
    border-radius: 2px;
    margin: 0 0 25px 50px;
    display: flex;
    flex-flow: column;
    background-color: white;
    position: fixed;
    top: 7vh;
    left: 20vw;
}

.add-trip-P {
    margin: 15px 0 0 15px;
    font-weight: 700;
}

.div-under-addTrip {
    display: grid;
    grid-template-columns: 145px 590px;
    grid-template-rows: 35px 35px 35px 35px 35px 35px;
    grid-column-gap: 35px;
    grid-row-gap: 25px;
    margin-top: 30px;
    padding-top: 30px;
    padding-bottom: 10px;
    border-top: 1px solid gray;
    margin-left: 15px;
}

.div-under-addTrip label {
    justify-self: end;
    font-weight: 700;
    align-self: center;
    text-align: center;
}

.grid-with-checkbox {
    display: grid;
    grid-template-columns: 100px 50px 150px;
    grid-template-rows: 20px 20px 20px 40px;
    grid-row-gap: 5px;
    margin: 20px 0 0 10px;
}

.grid-with-checkbox input {
    justify-self: end;
    align-self: end;
}

.grid-with-checkbox label {
    align-self: self-end;
}

.grid-with-checkbox-P {
    justify-self: end; 
    font-weight: 700;
    align-self: center;
}

.saveButtonTrip {
    width: 60px;
    height: 35px;
    margin-top: 10px;
    border: 1px solid #CDD6D5;
    border-radius: 4px;
}

.cancelTripButton {
    align-self: flex-end;
    margin-right: 15px;
    width: 70px;
    height: 35px;
    border: 1px solid #CDD6D5;
    border-radius: 4px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.passengers {
    display: flex;
    padding-left: 80px;
    margin-top: 15px;
    border-bottom: 1px solid gray;
    padding-bottom: 20px;
}

.passengers label {
    font-weight: 700;
}

#passengersCheckBoxList {
    display: flex;
    flex-flow: column;
    margin-left: 10px;
    margin-top: 3px;
}
#addPassenger {
    width: 800px;
    height: 400px;
    border: 1.3px solid black;
    border-radius: 2px;
    margin: 0 0 25px 50px;
    display: flex;
    flex-flow: column;
    background-color: white;
    position: fixed;
    left: 20vw;
    top: 10vh;
}

.add-Passenger-P {
    margin: 15px 0 15px 15px;
    font-weight: 700;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
}

.div-under-addPassenger {
    display: grid;
    grid-template-columns: 100px 640px;
    grid-template-rows: 35px 35px 35px 35px 35px;
    grid-column-gap: 40px;
    grid-row-gap: 15px;
    margin-top: 30px;
    border-bottom: 1px solid gray;
    padding-bottom: 10px;
}

.div-under-addPassenger label {
    justify-self: end;
    font-weight: 700;
    align-self: center;
}

.div-under-addPassenger input {
    border: 1.5px solid #CDD6D5;
    border-radius: 3px;
}

.addPassengerButton {
    grid-area: 5 / 2 / 5 / 2; 
    width: 50px;
    height: 35px;
    border: 1px solid #CDD6D5;
    border-radius: 4px;
}

.cancelButton {
    align-self: flex-end;
    margin-right: 15px;
    margin-top: 15px;
    width: 70px;
    height: 35px;
    border: 1px solid #CDD6D5;
    border-radius: 4px;
}
